body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.task {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
}

.task__title {
  padding: 5px;
}

.task__specification {
  color: #8d8173;
  text-decoration: none;
}

.task__specification:hover {
  opacity: 0.7;
}

.task__process {
  text-align: center;
  height: 20px;
  width: 40%;
  position: relative;
}

.task__process::before {
  content: '';
  position: absolute;
  left: 0;
  width: 0;
	height: 0;
	border-top: 10px solid transparent;
	border-left: 5px solid #e1e3e0;
	border-bottom: 10px solid transparent
}

.task__process_checked {
  color: #fff;
  background: #50c878;
}

.task__process_todo {
  color: #fff;
  background: #999;
}

.task__process_inprocess {
  color: #8d8173;
  background: #ffe5b4;
}

.task__process_tocheck {
  color: #fff;
  background: #f56433;
}

.task__process_nochecked {
  color: #fff;
  background: #960018;
}

.task__process_tooltip {
  display: none;
  padding: 4px 6px;
  width: 130px;
}

.task__process:hover .task__process_tooltip {
  display: inline;
  font-size: 12px;
  z-index: 1;
  position : absolute;
  right: 20px;
  bottom: 20px;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #ccc;
  border-radius: 5px;
  color: #8d8173;
}
.students__data {
  margin: 10px 5px;
  color: #8d8173;
  font-weight: 300;
  background: #e1e3e0;
  border-radius: 0 18px;
  width: 24%;
  position: relative;
}

.students__title {
  margin: 10px 5px;
  font-size: 16px;
  font-weight: 500;
}

.students__github {
  position: absolute;
  right: 5px;
  top: 10px;
}

.students__github-link {
  text-decoration: none;
  margin: 0 5px;
  color: #000;
}

.students__github-link:hover {
  opacity: 0.8;
}

.private {
  color: #ca002a;
}
.find-form {
  width: 25%;
}
.root__title {
  font-size: 28px;
  font-weight: 500;
  margin: 12px;
}

section {
  text-align: left;
  padding: 10px;
  }

.mentor {
  padding: 12px 10px;
  color: #f56433;
}

.mentor__title {
  font-size: 22px;
  font-weight: 500;
  margin: 0;
}

.mentor__data {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 12px;
  border-bottom: 2px solid #f56433;
}

.mentor__data-tooltip {
  font-size: 14px;
  margin-left: 22px;
}

.students {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  width: 100%;
}

.hidden {
  display: none;
}
